import {useState, useEffect, useRef}  from 'react'
import {useSpring, animated} from 'react-spring'
import * as easings from 'd3-ease'

const HomeProjects = ({fontSize, setPage, page}) => {
    
    const projects = ["Dummy One", "Dummy Two"]
    const projectClass = ["proj-one", "proj-two"]

    const html = document.querySelector('html')

    const backRef = useRef(null)
    const loaderRef = useRef(null)
    
    const [scrollPos, setScrollPos] = useState(0)
    const [scrollDir, setScrollDir] = useState('down')
    const [projectViewActive, setProjectView] = useState(false)
    const [headerAnimated, setHeaderAnimate] = useState(false)
    const [projectIndex, setProjectIndex] = useState(0)

    const HeroImage = () => useSpring({
        from: {
            marginLeft: -50,
            opacity: 0
        }, 
        to: {
            marginLeft: 0,
            opacity: 1
        },
        delay: 8200,
        config: {
            duration: 600,
            easing: easings.easeCubic
        }
    })    

    const Appear = useSpring({
        to: {
            opacity: 1
        },
        from : {
            opacity: 0
        },
        delay: 9400,
        config: {
            duration: 600,
            easing: easings.easeCubic
        }
    })

    const scrollHandler = () => {
        const currentPos = window.scrollY
        if(currentPos > scrollPos){
            setScrollDir('down')
        } else {
            setScrollDir('up')
        }
        setScrollPos(currentPos)
        setProjectView(((currentPos >= backRef.current.offsetTop)))        
        setTimeout(() => {
            setHeaderAnimate(((currentPos >= backRef.current.offsetTop)))
        }, 300);
        if(((currentPos >= backRef.current.offsetTop))){
            html.classList.add("html-project-active")
            projChange(projectIndex+1)
        } else {
            html.classList.remove("html-project-active")
        }
    }

    const projChange = (index) => {
        if(((window.scrollY >= backRef.current.offsetTop))){
            console.log(index%projects.length)
            setTimeout(() => {
                setHeaderAnimate(true)
            }, 300)
            var changer = setTimeout(() => {
                if(((window.scrollY >= backRef.current.offsetTop))){
                    setProjectIndex((index)%projects.length)
                    setHeaderAnimate(false)
                    projChange(index+1)
                }
            }, 10000)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', scrollHandler)
        return () => window.removeEventListener('scroll', scrollHandler)
    },[])

    return[
        <animated.div className={`image-holder${(projectViewActive && (page === "Home"))?' image-holder-active':''}`} style={HeroImage()} ref={backRef}>
            <div className={`project${projectViewActive?' project-active':''} ${projectClass[projectIndex%2]}`}>
                <h1>Work</h1>
                <div>
                    <animated.h1 className={`${(headerAnimated && (page === "Home"))?'active-header':''}`} >{projects[projectIndex%2]}</animated.h1>
                    <div className="project-loader" ref={loaderRef}></div>
                </div>
            </div>
        </animated.div>,
        <animated.div className={`side-links-left ${(projectViewActive && (page === "Home"))?'side-links-left-dark':''}`} style={Appear}>
            <a href="https://www.linkedin.com/in/prateek-bose-790a4316b/"><h1>linkedin</h1></a>
            <a href="#"><h1>behance</h1></a>
            <a href="https://github.com/prateekbose"><h1>github</h1></a>
        </animated.div>,
        <animated.h1 className={`side-links-right ${((projectViewActive && (page === "Home"))?'hide-text':'')}`} style={Appear}>&#10229; scroll to see more</animated.h1>,
        <animated.h1 className={`side-links-right see-more${((projectViewActive && (page === "Home"))?' see-more-active':'')}`} style={Appear}>see more work</animated.h1>
    ]
}

export default HomeProjects