import React, { useEffect } from 'react'
import HomeProjects from './homeProjects'
import {useSpring, animated} from 'react-spring'
import SplitText from './splitText'
import * as easings from 'd3-ease'

const HeroSection = ({fontSize, setPage, page, html}) => {

    const header = "I'm Prateek, a \nUI/UX Designer \nand a \nFull-Stack Web Developer\n. Striving to create amazing experiences\n, on web and mobile devices.".split("\n")

    const Shift = useSpring({
        to: {
            marginTop: 0
        }, 
        from: {
            marginTop: window.innerHeight/4
        }, 
        delay: 7800,
        config: {
            duration: 600,
            easing: easings.easeCubic
        }
    })

    useEffect(() => {
        const PageNoScroll = () => {
            html.classList.add('no-scroll')
            setTimeout(() => {
                html.classList.remove('no-scroll')
            }, 8800)
        }
        PageNoScroll()
    })

    return [
        <div className="hero">
            <animated.h1 className="hero-header" style={Shift}>
                <SplitText bold={["UI/UX Designer ", "Full-Stack Web Developer"]}>
                    {header}
                </SplitText>
            </animated.h1>
        </div>,
        <HomeProjects fontSize={fontSize} setPage={setPage} page={page}/>
    ]
}

export default HeroSection